import { Component, h, Element, State, Host } from '@stencil/core';
import { ICellRendererComp, ICellRendererParams } from 'ag-grid-community';

export interface IEditTextCellParams {
    writePermission?: boolean;
    validator?: (value: string) => boolean;
    maxLength?: string;
    onValueChange: (params: ICellRendererParams) => void;
}

interface InitParams extends IEditTextCellParams, ICellRendererParams {}

/**
 * @category Table Cell
 * @description A cell component for displaying and editing text.
 */

@Component({
    tag: 'atui-edit-text-cell',
    shadow: false,
})
export class AtuiEditTextCell implements ICellRendererComp {
    @Element() el: HTMLElement;
    @State() params: InitParams;

    init(params: InitParams): void {
        this.params = params;
    }

    getGui(): HTMLElement {
        return this.el;
    }

    refresh(params: InitParams): boolean {
        this.updateCell(params.value);
        return true;
    }

    protected updateCell(newValue: any) {
        this.params.value = newValue;
        if (this.params.onValueChange) {
            this.params.onValueChange(this.params);
        }
    }

    render() {
        return (
            <Host class="flex h-full items-center leading-[100%]">
                <atui-input
                    readonly={!this.params.writePermission}
                    clearable={true}
                    value={this.params.value}
                    onInput={(event) =>
                        this.updateCell(
                            (event.target as HTMLInputElement).value,
                        )
                    }
                    onMouseDown={(event) => event.stopPropagation()}
                ></atui-input>
            </Host>
        );
    }
}
